#touchBarUL {
  height: 100%;
  width: 90%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

#touchBarUL li {
  height: 10%;
  width: 100%;
  border: solid;
  border-width: 1px 1px 0px 1px;
}

#touchBarUL li.firstBarItem {
  border: solid;
  border-width: 1px 1px 1px 1px;
}



